@import './var';

.menu-container {
  background: #fff;

  .menuContentBox {
    margin: 10px auto;
    @media (max-width: 359px) {
      margin: 5px auto;
    }
    h1 {
      /* ${props => props.theme.fontLeckerli} */
      // font-family: $fontRobotoSlab;
      // font-size: 40px;
      color: $darkNavy;
      text-align: center;
      font-family: $fontAmatic;
      font-size: 64px;
      font-weight: 700;
      line-height: 1.4;
      @media (max-width: 767px) {
        font-size: 44px;
      }
    }
    .itemsBox {
      margin-top: 10px;
      padding: 20px 100px;
      @media (max-width: 1023px) {
        padding: 40px;
      }
      @media (max-width: 767px) {
        padding: 10px;
      }
      @media (max-width: 359px) {
        // padding: 4px;
        padding: 6px;
      }
      .itemBoxGrid {
        display: grid;
        grid-gap: 20px;
        grid-template-columns: 1fr 1fr;

        @media (max-width: 767px) {
          grid-gap: 10px;
          grid-template-columns: 1fr;
        }

        /* grid-template-columns: repeat(auto-fit, minmax(400px, 1fr)); */

        .itemBox {
          /* background: rgba(255, 0, 0, 0.5); */
          display: flex;
          justify-content: center;
          .imageBox {
            overflow: hidden;
            width: 160px;
            height: 160px;
            border-radius: 80px;
            @media (max-width: 1023px) {
              width: 140px;
              height: 140px;
              border-radius: 70px;
            }
            /* width: 200px;
            height: 140px;
            border-radius: 8px; */
            /* position: relative; */
            img {
              /* position: absolute;
              left: 50%;
              transform: translateX(-50%); */
              // -webkit-transform: translateZ(0);
              // -webkit-perspective: 1000;
              -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
              // width: 160px;
              // height: 160px;
              // border-radius: 80px;
              width: 100%;
              height: 100%;
              user-select: none;
              filter: brightness(110%);
              // @media (max-width: 1023px) {
              //   width: 140px;
              //   height: 140px;
              //   border-radius: 70px;
              // }
            }
          }
          .contentBox {
            flex: 1;
            margin-left: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @media (max-width: 767px) {
              margin-left: 10px;
            }
            .name {
              position: relative;
              // font-family: $fontPlayfair;
              font-family: $fontRobotoSlab;
              // ${props => props.theme.fontPlayfair}
              color: $black;
              font-weight: bold;
              font-size: 18px;
              line-height: 1.5;

              .nameDescription {
                position: absolute;
                font-size: 10px;
                // top: 30%;
                top: 40%;
                margin-left: 4px;
                // color: lighten($darkNavy, 10%);
                color: $lightNavy;
                @media (max-width: 359px) {
                  top: 35%;
                  font-size: 9px;
                  margin-left: 2px;
                }
              }
              .day {
                position: absolute;
                font-size: 12px;
                font-style: italic;
                // margin-top: -12px;
                margin-top: -18px;

                top: 0;
                left: 10%;
                padding: 1px 6px;
                border-radius: 9px;

                background: $green;
                color: $offWhite;
                // @media (max-width: 1024px) {
                //   margin-top: -18px;
                //   left: 55%;
                // }
                // @media (max-width: 767px) {
                //   margin-top: -18px;
                //   left: 0;
                // }
                // @media (max-width: 565px) {
                //   margin-top: -16px;
                //   left: 35%;
                // }
                // @media (max-width: 359px) {
                //   left: 25%;
                // }
              }
            }
            .price {
              font-size: 18px;
              font-weight: bold;
              font-family: $fontPlayfair;
              // ${props => props.theme.fontPlayfair}
              // color: ${props => props.theme.darkRed};
              color: lighten($darkRed, 5%);

              .striked {
                font-family: $fontRobotoSlab;
                color: $darkRed;
                text-decoration: line-through;
              }

              .dollar-sign {
                font-family: $fontRobotoSlab;
                margin-right: 2px;
              }
            }
            .description {
              color: $grey;
              font-style: italic;
              line-height: 1.2;
            }
          }
        }
      }
    }
  }
}
